
#app {
  background: white;
  width: 100%;
  .app-header {
    margin: auto;
    width: 100%;
    .header-panel {
      margin: auto;
      width: 940px;
      min-width: 940px;
      max-width: 940px;
      height: 76px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .header-logo {
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          cursor: pointer;
          display: block;
          height: 33px;
        }
      }
      .header-menu {
        width: 810px;
        max-width: 820px;
        height: 76px;
        font-size: 14px;
        color: $favorite;
        font-weight: 600;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .header-menu-items {
          float: left;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          .header-menu-item {
            margin-left: 38px;
            cursor: pointer;
          }
          .header-menu-item.active {
            color: $positive;
          }
          .header-menu-item.start-menu {
            position: relative;
            img {
              position: absolute;
              width: 15px;
              height: 15px;
              top: -10px;
              right: -10px;
            }
          }
        }
        .header-account-items {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          .header-account-item {
            padding: 12px 24px;
            border-radius: 24px;
          }
          .header-account-item.register {
            background: $positive;
            color: white;
          }
        }
      }
    }
  }
  .app-footer {
    background: $background;
    height: 250px;
    .footer-panel {
      margin: auto;
      width: 940px;
      min-width: 940px;
      max-width: 940px;
      display: flex;
      justify-content: space-between;
      padding: 32px 0;
      color: $dissolve;
      font-size: 12px;
      .footer-sitemap, .footer-company {
        width: 50%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
      }
      .footer-sitemap {
        text-align: left;
        padding: 0 10%;
        .sitemap-label {
          margin-bottom: 26px;
          color: $emphasize;
          font-weight: 600;
        }
        .sitemap-menu-row {
          width: 100%;
          display: flex;
          justify-content: flex-start;
          .sitemap-menu-item {
            width: 50%;
            margin-top: 12px;
          }
        }
      }
      .footer-company {
        text-align: left;
        font-size: 12px;
        .company-name {
          margin-bottom: 5px;
          height: 30px;
          img {
            display: block;
            height: 100%;
          }
        }
        .company-description {
          margin-top: 15px;
        }
        .company-sns {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          margin-top: 30px;
          font-size: 8px;
          font-weight: 600;
          letter-spacing: 4px;
          word-spacing: 8px;
          height: 15px;
          img {
            margin-left: 20px;
            height: 100%;
            display: block;
          }
        }
      }
    }
  }
}

