body {
  font-family: 'RobotoDraft', 'Roboto', 'Helvetica Neue, Helvetica, Arial', sans-serif;
  text-align: center;
  font-style: normal;
  background-color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  //  font-weight: 300;
  //  font-size: 1.4rem;
  //  line-height: 2rem;
  //  letter-spacing: 0.01rem;
  //  color: #212121;
}

.full-screen {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.panel-content {
  padding: 5px;
}

.panel-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: $light;

}

.font {
  z-index: 1000;
}

// common class
.alert-danger {
  color: red;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

// material card style
md-card {
  padding: 12px !important;
}

md-card-title {
  font-size: 18px !important;
}

md-card {
  margin-bottom: 5px;
  md-card-header {
    //background: $favorite;
  }
}

md-card-actions {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.dialog-buttons {
  background-color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}



