.datatable [hidden] {
  display: none !important;
}

.datatable *, .datatable *:before, .datatable *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.datatable.scroll-vertical .datatable-body {
  overflow-y: auto;
}

.datatable.scroll-vertical .datatable-body .datatable-body-row {
  position: absolute;
}

.datatable.scroll-horz .datatable-body {
  overflow-x: auto;
}

.datatable.fixed-header .datatable-header .datatable-header-inner {
  white-space: nowrap;
}

.datatable.fixed-header .datatable-header .datatable-header-inner .datatable-header-cell {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.datatable.fixed-row .datatable-scroll {
  white-space: nowrap;
}

.datatable.fixed-row .datatable-scroll .datatable-body-row {
  white-space: nowrap;
}

.datatable.fixed-row .datatable-scroll .datatable-body-row .datatable-body-cell {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.datatable .datatable-body-row,
.datatable .datatable-header-inner {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  -webkit-flex-flow: row;
  -moz-flex-flow: row;
  -ms-flex-flow: row;
  -o-flex-flow: row;
  flex-flow: row;
}

.datatable .datatable-body-cell,
.datatable .datatable-header-cell {
  vertical-align: top;
  display: inline-block;
  line-height: 1.625;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.datatable .datatable-row-left,
.datatable .datatable-row-right {
  z-index: 9;
}

.datatable .datatable-row-left,
.datatable .datatable-row-center,
.datatable .datatable-row-right {
  position: relative;
}

.datatable .datatable-header {
  display: block;
  overflow: hidden;
}

.datatable .datatable-header .datatable-header-inner {
  align-items: stretch;
  -webkit-align-items: stretch;
}

.datatable .datatable-header .datatable-header-cell {
  position: relative;
  display: inline-block;
}

.datatable .datatable-header .datatable-header-cell.longpress {
  cursor: move;
}

.datatable .datatable-header .datatable-header-cell.sortable {
  cursor: pointer;
}

.datatable .datatable-header .datatable-header-cell .sort-btn {
  line-height: 100%;
  vertical-align: middle;
  display: inline-block;
}

.datatable .datatable-header .datatable-header-cell .resize-handle {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 5px;
  padding: 0 8px;
  visibility: hidden;
  cursor: ew-resize;
}

.datatable .datatable-header .datatable-header-cell.resizeable:hover .resize-handle {
  visibility: visible;
}

.datatable .datatable-body {
  position: relative;
  z-index: 10;
  display: block;
}

.datatable .datatable-body .datatable-scroll {
  display: inline-block;
}

.datatable .datatable-body .datatable-body-row {
  outline: none;
}

.datatable .datatable-body .datatable-body-row > div {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

.datatable .datatable-footer {
  display: block;
  width: 100%;
}

.datatable .datatable-footer > div {
  display: flex;
  align-items: center;
  width: 100%;
}

.datatable .datatable-footer .page-count {
  display: inline-block;
  flex: 1 1 20%;
}

.datatable .datatable-footer .datatable-pager {
  display: inline-block;
  flex: 1 1 80%;
  text-align: right;
}

.datatable .datatable-footer .datatable-pager .pager,
.datatable .datatable-footer .datatable-pager .pager li {
  padding: 0;
  margin: 0;
  display: inline-block;
  list-style: none;
}

.datatable .datatable-footer .datatable-pager .pager li, .datatable .datatable-footer .datatable-pager .pager li a {
  outline: none;
}

.datatable .datatable-footer .datatable-pager .pager li a {
  cursor: pointer;
  display: inline-block;
}

.datatable .datatable-footer .datatable-pager .pager li.disabled a {
  cursor: not-allowed;
}

/**
 * material Styles
 */

.datatable.material {
  background: #FFF;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);
  -moz-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);

  &.striped {
    .datatable-row-odd {
      background: #eee;
    }
  }

  /**
   * Shared Styles
   */
  .empty-row {
    height: 50px;
    text-align: left;
    padding: .5rem 1.2rem;
    vertical-align: top;
    border-top: 0;
  }

  .loading-row {
    text-align: left;
    padding: .5rem 1.2rem;
    vertical-align: top;
    border-top: 0;
  }

  /**
   * Global Row Styles
   */
  .datatable-header,
  .datatable-body {
    .datatable-row-left {
      background-color: #FFF;
      background-position: 100% 0;
      background-repeat: repeat-y;
      background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAABCAYAAAD5PA/NAAAAFklEQVQIHWPSkNeSBmJhTQVtbiDNCgASagIIuJX8OgAAAABJRU5ErkJggg==);
    }

    .datatable-row-right {
      background-position: 0 0;
      background-color: #fff;
      background-repeat: repeat-y;
      background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAABCAYAAAD5PA/NAAAAFklEQVQI12PQkNdi1VTQ5gbSwkAsDQARLAIGtOSFUAAAAABJRU5ErkJggg==);
    }
  }

  /**
   * Header Styles
   */
  .datatable-header {
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);

    .datatable-header-cell {
      text-align: left;
      padding: .5rem 1.2rem;
      font-weight: 400;
      color: #757575;
      vertical-align: bottom;

      &.longpress {
        color: #00E676;
      }

      &.dragging {
        .resize-handle {
          border-right: none;
        }
      }
    }

    .resize-handle {
      border-right: solid 1px #eee;
    }
  }

  /**
   * Body Styles
   */
  .datatable-body {
    .datatable-body-row {
      .datatable-body-cell {
        text-align: left;
        padding: 2px 5px;
        vertical-align: top;
        border-top: 0;

        -webkit-transition: width 0.3s ease;
        -o-transition: width 0.3s ease;
        transition: width 0.3s ease;
      }

      .datatable-row-left,
      .datatable-row-center,
      .datatable-row-right {

      }

      &:hover,
      &:hover .datatable-row-group {
        background-color: #eee;
        transition-property: background;
        transition-duration: .3s;
        transition-timing-function: linear;
      }

      &:focus,
      &:focus .datatable-row-group {
        background-color: #ddd;
      }

      &.active,
      &.active:focus,
      &.active:hover {
        background-color: #304FFE;
        color: #FFF;
      }
    }

    .progress-linear {
      display: block;
      position: relative;
      width: 100%;
      height: 5px;
      padding: 0;
      margin: 0;
      position: absolute;

      .container {
        display: block;
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 5px;
        -webkit-transform: translate(0, 0) scale(1, 1);
        transform: translate(0, 0) scale(1, 1);
        background-color: rgb(170, 209, 249);

        .bar {
          transition: all .2s linear;
          -webkit-animation: query .8s infinite cubic-bezier(.39, .575, .565, 1);
          animation: query .8s infinite cubic-bezier(.39, .575, .565, 1);

          transition: -webkit-transform .2s linear;
          transition: transform .2s linear;
          background-color: rgb(16, 108, 200);

          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          width: 100%;
          height: 5px;
        }
      }
    }
  }

  /**
   * Footer Styles
   */
  .datatable-footer {
    border-top: 1px solid rgba(0, 0, 0, 0.12);
    font-size: 16px;

    .page-count {
      padding: 0 1.2rem;
    }

    .datatable-pager {
      margin: 0 10px;
      //line-height: 50px;

      li {
        vertical-align: middle;

        &.disabled a {
          color: rgba(0, 0, 0, 0.26) !important;
          background-color: transparent !important;
        }

        &.active a {
          background-color: rgba(158, 158, 158, 0.2);
          font-weight: bold;
        }
      }

      a {
        height: 22px;
        min-width: 24px;
        line-height: 22px;
        padding: 0 6px;
        border-radius: 3px;
        margin: 6px 3px;
        text-align: center;
        vertical-align: top;
        color: rgba(0, 0, 0, .54);
        text-decoration: none;
        vertical-align: bottom;

        &:hover {
          color: rgba(0, 0, 0, .75);
          background-color: rgba(158, 158, 158, 0.2);
        }
      }

      .icon-left,
      .icon-skip,
      .icon-right,
      .icon-prev {
        font-size: 20px;
        line-height: 20px;
        padding: 0 3px;
      }
    }
  }
}

/**
 * Progress bar animations
 */
@-webkit-keyframes query {
  0% {
    opacity: 1;
    -webkit-transform: translateX(35%) scale(.3, 1);
    transform: translateX(35%) scale(.3, 1);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateX(-50%) scale(0, 1);
    transform: translateX(-50%) scale(0, 1);
  }
}

@keyframes query {
  0% {
    opacity: 1;
    -webkit-transform: translateX(35%) scale(.3, 1);
    transform: translateX(35%) scale(.3, 1);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateX(-50%) scale(0, 1);
    transform: translateX(-50%) scale(0, 1);
  }
}



