pager {
  display: inline-block;
  flex: 1 1 80%;
  text-align: right;
  margin: 0 10px;
  .pager,
  .pager li {
    padding: 0;
    margin: 0;
    display: inline-block;
    list-style: none;
  }
  .pager {
    li, li a {
      outline: none;
    }

    li {
      a {
        cursor: pointer;
        display: inline-block;
      }

      &.disabled a {
        cursor: not-allowed;
      }
    }
  }
  li {
    vertical-align: middle;

    &.disabled a {
      color: rgba(0, 0, 0, 0.26) !important;
      background-color: transparent !important;
    }

    &.active a {
      background-color: rgba(158, 158, 158, 0.2);
      font-weight: bold;
    }
  }

  a {
    height: 22px;
    min-width: 24px;
    line-height: 22px;
    padding: 0 6px;
    border-radius: 3px;
    margin: 6px 3px;
    text-align: center;
    vertical-align: top;
    color: rgba(0, 0, 0, .54);
    text-decoration: none;
    vertical-align: bottom;

    &:hover {
      color: rgba(0, 0, 0, .75);
      background-color: rgba(158, 158, 158, 0.2);
    }
  }

  .icon-left,
  .icon-skip,
  .icon-right,
  .icon-prev {
    font-size: 20px;
    line-height: 20px;
    padding: 0 3px;
  }
}

