templates {
  .content {
    .category-selector {
      width: 100%;
      height: 44px;
      background: $background;
      border-top: solid 2px $liner;
      border-bottom: solid 2px $liner;
      .category-panel {
        margin: auto;
        width: 940px;
        min-width: 940px;
        max-width: 940px;
        height: 44px;
        background: $background;
        display: flex;
        justify-content: space-around;
        align-items: center;
        font-size: 14px;
        color: $favorite;
        font-weight: 600;
        position: relative;
        .category-item {
          width: 15%;
          height: 44px;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          .category-label {
            position: relative;
            img.category-check {
              display: block;
              height: 10px;
              top: 2px;
              left: -20px;
              position: absolute;
            }
          }
          .sub-category {
            width: 105px;
            top: 44px;
            background: white;
            padding: 7px 15px 7px 15px;
            position: absolute;
            border-top: solid 2px $positive;
            font-size: 12px;
            font-weight: 600;
            text-align: left;
            display: none;
            box-shadow: 0 2px 15px 2px rgba(0, 0, 0, 0.1);
            .sub-category-item {
              margin-top: 1px;
              padding: 7px 0;
              color: $favorite;
            }
            .sub-category-item.active {
              color: $positive;
            }

          }
          .sub-category.selected {
            display: block;
          }

        }
        .category-item.active {
          color: black
        }
        .category-item:hover .sub-category {
          display: block;
        }

      }
    }
    .category-content {
      margin: auto;
      width: 940px;
      .template-title {
        color: $dark;
        font-weight: 100;
        font-size: 32px;
        margin-top: 56px;
      }
      .template-sub-title {
        margin-top: 50px;
        .template-sub-title-label1 {
          color: $dark;
          font-size: 16px;
        }
        .template-sub-title-label2 {
          margin-top: 12px;
          text-align: center;
          font-size: 14px;
          color: $dark;
        }
      }
      .template-keyword-selector {
        margin: 56px 10% 0 10%;
        height: 90px;
        width: 80%;
        display: flex;
        justify-content: flex-start;
        .template-keyword {
          cursor: pointer;
          margin-left: -1px;
          display: flex;
          justify-content: center;
          align-items: center;
          color: $favorite;
          width: 12.5%;
          height: 90px;
          border: solid 1px $liner;
          font-size: 12px;
          .template-keyword-label {
            position: relative;
            img.template-keyword-new {
              width: 10px;
              position: absolute;
              top: -12px;
              right: -12px;
            }
          }

        }
        .template-keyword.active {
          border: solid 1px $positive;
          color: $positive;
          z-index: 10;
        }
      }
      .template-search-result {

        text-align: left;
        color: $dark;
        margin-top: 28px;
        font-size: 12px;
      }
      .template-search-filter {
        margin-top: 16px;
        width: 940px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .template-search-sort {
          font-size: 12px;
          display: flex;
          justify-content: flex-start;
          color: $dark;
          .sort-item {
            padding-right: 28px;
            cursor: pointer;
            position: relative;
            img {
              position: absolute;
              height: 5px;
              top: 3px;
              right: 10px;
              display: none;
            }
          }
          .sort-item:last-child {
            padding-left: 28px;
          }
          .sort-item.active {
            font-weight: 600;
            img {
              display: block;
            }
          }
          .vertical-ruler {
            width: 1px;
            height: 12px;
            border-right: solid 1px $dark;
          }
        }
        .template-color {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          .template-color-label {
            font-size: 12px;
            color: $favorite;
            margin-right: 10px;
          }
          .template-color-selector{
            width: 122px;
            height: 22px;
            background: $background;
            font-size: 10px;
            font-weight: 600;
            padding: 0 8px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: $dissolve;
            border: solid 1px $liner;
            position: relative;
            cursor: pointer;
            .template-color-options{
              display: none;
              position: absolute;
              width: 100%;
              top: 22px;
              left: 0px;
              right: 0px;
              z-index: 10;
              padding: 6px 0;
              background: white;
              box-shadow: 0 2px 15px 2px rgba(0, 0, 0, 0.1);
              .template-color-item{
                cursor: pointer;
                display: flex;
                justify-content: space-between;
                align-items: center;
                height: 34px;
                padding: 6px 10px;
                .template-color-panel{
                  cursor: pointer;
                  display: flex;
                  justify-content: flex-start;
                  align-items: center;
                  img{
                    cursor: pointer;
                    height: 22px;
                    display: block;
                  }
                  span{
                    cursor: pointer;
                    margin-left: 8px;
                  }
                }
                .template-color-selected{
                  cursor: pointer;
                  width: 8px;
                }
              }
            }
            .template-color-options.active{
              display: block;
            }
          }
        }
      }
      .template-list {
        margin-top: 25px;
        .template-list-item {
          width: 100%;
          height: 100%;
          border: solid 1px $liner;
          display: flex;
          justify-content: flex-start;
          flex-direction: column;
          .template-thumbnail-container {
            width: 100%;
            position: relative;
            .template-hover-menu {
              opacity: 0;
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              background: rgba(0, 0, 0, 0.6);
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: column;
              .template-hover-btn {
                padding: 10px 0;
                width: 90px;
                background: white;
                color: $favorite;
                font-size: 14px;
                font-weight: 600;
                border-radius: 15px;
                cursor: pointer;

              }
              .template-hover-btn:hover {
                font-weight: 600;
                background: $positive;
                color: white;
              }
              .spacer {
                height: 15px;
              }
            }
          }
          .template-thumbnail-container:hover {
            .template-hover-menu {
              opacity: 1;
            }
          }
          .template-list-item-content {
            padding: 10% 0;
            flex-grow: 1;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            align-items: center;
            .template-list-item-name {
              cursor: pointer;
              font-size: 18px;
            }
            .template-list-item-price-container {
              font-size: 12px;
              color: $positive;
              .template-list-item-price {
                color: $favorite;
                text-decoration: line-through;
              }
            }
            .template-list-item-action-container {
              width: 100%;
              display: flex;
              justify-content: center;
              .template-list-item-like {
                cursor: pointer;
                width: 20px;
                height: 20px;
                background-image: url('img/ic_heart_default.png');
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center center;
              }
              .template-list-item-like:hover {
                background-image: url('img/ic_heart_active.png');
              }
              .spacer {
                width: 14%;
              }
              .template-list-item-cart {
                cursor: pointer;
                width: 20px;
                height: 20px;
                background-image: url('img/ic_cart_default.png');
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center center;
              }
              .template-list-item-cart:hover {
                background-image: url('img/ic_cart_active.png');
              }
            }
          }
        }
      }
      .template-list-pager {
        width: 100%;
        height: 100%;
        margin-top: 75px;
        position: relative;
        .template-list-top {
          position: absolute;
          right: 0;
          top: 0;
          img {
            cursor: pointer;
            width: 40px;
            display: block;
          }
        }
      }
      .content-spacer::before {
        display: block;
        content: "";
        padding-top: 70px;
      }
    }
  }
}