template-detail {
  .template-detail-container {
    width: 800px;
    > md-grid-list > div > md-grid-tile > figure {
      padding: 30px 0;
    }
    .preview-section {
      border-right: solid 1px $liner;
      figure {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        height: auto;
        .preview-label {
          font-size: 20px;
          font-weight: 500;
        }
        .preview-type-switcher {
          margin-top: 28px;
          display: flex;
          justify-content: center;
          align-items: center;
          md-button-toggle-group {
            box-shadow: none;
            font-size: 14px;
            md-button-toggle:first-child {
              .md-button-toggle-label-content {
                border-top-left-radius: 15px;
                border-bottom-left-radius: 15px;
                margin-right: -1px;
              }
            }
            md-button-toggle:last-child {
              .md-button-toggle-label-content {
                border-top-right-radius: 15px;
                border-bottom-right-radius: 15px;
              }
            }
            .md-button-toggle-label-content {
              width: 145px;
              height: 30px;
              line-height: 30px;
              color: $favorite;
              border: solid 1px $liner;
              padding: 0;
            }
            .md-button-toggle-checked {
              .md-button-toggle-label-content {
                font-weight: 500;
                background: $background;
                color: $dark;
              }
            }
          }
        }

        .template-view-panel {
          width: 100%;
          .preview-device-switcher {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 12px;
            md-slide-toggle {
              .md-slide-toggle-container {
                margin-right: 0px;
                .md-slide-toggle-bar {
                  background-color: $background;
                  border: solid 1px $liner;
                  top: 2px;
                  bottom: 0;
                  height: 18px;
                  border-radius: 20px;
                }
              }
              .md-slide-toggle-thumb {
                box-shadow: 0px 0px 8px -1px rgba(0, 0, 0, 0.2),
                0px 0px 8px 0px rgba(0, 0, 0, 0.14),
                0px 2px 8px 0px rgba(0, 0, 0, 0.12);
              }
            }
            md-slide-toggle.md-checked:not(.md-disabled) .md-slide-toggle-thumb {
              background-color: white;
            }
            md-slide-toggle.md-slide-toggle-focused {
              .md-ink-ripple {
                display: none;
              }
            }

            .label-ios {
              margin-right: 12px;
              font-weight: 600;
              color: $favorite;
            }
            .label-android {
              margin-left: 12px;
              font-weight: 600;
              color: $favorite;
            }
            .label-android.active, .label-ios.active {
              color: $dark;
            }
          }
          .preview-viewer-container {
            width: 100%;
            height: 620px;
          }
          .overview-screens {
            background-color: $liner;
            margin: 32px 20px;
            height: 650px;
            overflow-y: auto;
            width: calc(100% - 40px);
            font-size: 14px;
            color: $accent;
            .overview-page {
              figure {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                padding-top: 2px;
                .screen-shot {
                  width: calc(100% - 16px);
                  position: relative;
                  img {
                    width: 100%;
                    display: block;
                    margin: auto;
                  }
                  .screen-shot-hover {
                    cursor: zoom-in;
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    background-color: rgba(0, 0, 0, 0.6);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    opacity: 0;
                    button {
                      cursor: zoom-in;
                      border: solid 1px white;
                      border-radius: 20px;
                      margin: 0;
                      font-size: 10px;
                      color: white;
                      background-color: rgba(255, 255, 255, 0.5);
                      padding: 10px 20px;
                    }
                  }
                }
                .screen-shot-hover:hover {
                  opacity: 1;
                }
                .screen-name {
                  height: 100%;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                }
              }
            }
          }
        }
      }
    }
    .template-detail-info {
      figure {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        .shopping-section {
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          .template-name {
            font-size: 20px;
          }
          .template-price {
            font-size: 9px;
            color: $dissolve;
            text-decoration: line-through;
            margin-top: 15px;
          }
          .template-sale-price-container {
            font-size: 14px;
            color: $positive;
            margin-top: 6px;
            .template-sale-tag {
              margin-right: 4px;
            }
          }
          .template-shopping-action {
            margin-top: 18px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 10px;
            color: $favorite;
            .template-action-like, .template-action-cart {
              cursor: pointer;
            }
            .template-action-like {
              margin-right: 36px;
            }
            .template-action-like-icon {
              margin-bottom: 5px;
              display: flex;
              justify-content: center;
              align-items: center;
              height: 34px;
              width: 34px;
              border: solid 1px $dull;
              border-radius: 50%;
              background-image: url('img/ic_heart_default.png');
              background-repeat: no-repeat;
              background-size: auto 14px;
              background-position: center center;
            }
            .template-action-cart-icon {
              margin-bottom: 5px;
              display: flex;
              justify-content: center;
              align-items: center;
              height: 34px;
              width: 34px;
              border: solid 1px $dull;
              border-radius: 50%;
              background-image: url('img/ic_cart_default.png');
              background-repeat: no-repeat;
              background-size: auto 14px;
              background-position: center center;
            }

            .template-action-like:hover {
              color: $positive;
              .template-action-like-icon {
                border: solid 1px $positive;
                background-image: url('img/ic_heart_active.png');
              }
            }

            .template-action-cart:hover {
              color: $positive;
              .template-action-cart-icon {
                border: solid 1px $positive;
                background-image: url('img/ic_cart_active.png');
              }
            }

          }
          .template-action-release-container {
            margin-top: 18px;
            button {
              width: 220px;
              height: 52px;
              font-size: 16px;
              font-weight: 600;
              color: white;
              background: $positive;
              border: none;
              border-radius: 2px;
            }
          }
        }
        .divider {
          width: calc(100% - 30px);
          height: 1px;
          background: $liner;
          margin-top: 50px;
        }
        .detail-section {
          text-align: left;
          width: calc(100% - 28px);
          margin: 22px 0 0 14px;
          .detail-category, .detail-keyword, .detail-feature, .detail-description, .detail-recommendation-label {
            margin-top: 10px;
            font-size: 12px;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            line-height: 22px;
            .detail-label {
              margin-left: 10px;
              width: 64px;
            }
            .detail-value {
              color: $favorite;
              display: flex;
              align-items: flex-start;
              max-width: calc(100% - 74px);

            }
            .detail-value-list {
              color: $favorite;
              display: flex;
              justify-content: flex-start;
              align-items: flex-start;
              flex-wrap: wrap;
              .list-item {
                display: flex;
                justify-content: flex-start;
                align-items: flex-start;
                margin-bottom: 5px;
                span {
                  white-space: nowrap;
                }
                span.divider {
                  margin: 0 5px;
                }
              }
            }
            md-icon {
              margin: 0 3px 0 0;
              width: 12px;
              height: 12px;
              font-size: 16px;
              line-height: 22px;
            }
          }
          .detail-description {
            height: 120px;
            .detail-value {
              white-space: pre-wrap;
            }
          }
          .detail-recommendation-list {
            margin-top: 20px;
            position: relative;
            width: 100%;
            .prev-recommendation, .next-recommendation {
              cursor: pointer;
              position: absolute;
              top: 0;
              bottom: 0;
              height: 100%;
              width: 25px;
              z-index: 10;
              background-color: white;
              opacity: 0.7;
              display: flex;
              justify-content: center;
              align-items: center;
            }
            .next-recommendation {
              right: 0;
            }
            .prev-recommendation {
              left: 0;
            }
            .next-recommendation:hover, .prev-recommendation:hover {
              opacity: 0.9;
            }
            md-grid-list {
              width: 100%;
              .detail-recommendation-item {
                border: solid 1px $liner;
                .description {
                  width: 100%;
                  height: 100%;
                  display: flex;
                  justify-content: space-around;
                  align-items: center;
                  flex-direction: column;
                  padding: 10px 0;
                  .name {
                    font-size: 12px;
                  }
                  .original-price {
                    font-size: 8px;
                    color: $favorite;
                    text-decoration: line-through;
                  }
                  .price-container {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .sale-tag {
                      font-size: 10px;
                      color: $positive;
                      margin-right: 5px;
                    }
                    .sale-price {
                      font-size: 10px;
                      font-weight: 600;
                      color: $positive;

                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
