preload-image {
  width: 100%;
  display: block;
  position: relative;
  .preload-image-panel {
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    .preload-image-loading {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      //background-color: color($colors, favorite);
      display: flex;
      justify-content: center;
      align-items: center;
      opacity: 1;
      transition: all 0.2s ease;
    }
  }
  .preload-image-loading.done {
    opacity: 0;
  }
}