previewer {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: relative;
  margin: auto;
  div.previewer-container {
    background-image: url('img/device_s7.png');
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center center;
    margin: auto;
    width: 100%;
    height: 100%;
    position: relative;
    .no-preview-message {
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    iframe {
      position: absolute;
      top: 0;
      left: 0;
    }
    iframe.no-preview {
      opacity: 0;
    }
  }
}